import React from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

function PaginationComponent({
  page, hasPrevPage, hasNextPage, totalPages, onPageChange,
}) {
  const getPaginationItem = () => {
    const paginationItem = [];
    const interval = 5;
    let start;
    let end;

    if (page < totalPages - interval) {
      start = page;
      end = page + interval;
    } else if (totalPages > interval) {
      start = totalPages - interval;
      end = totalPages;
    } else {
      start = 1;
    }

    for (let i = start; i <= end; i += 1) {
      paginationItem.push(i);
    }

    return paginationItem;
  };

  return (
    <Pagination>
      {page > 1 && (
        <PaginationItem onClick={() => onPageChange(1)}>
          <PaginationLink first />
        </PaginationItem>
      )}
      {hasPrevPage && (
        <PaginationItem onClick={() => onPageChange((p) => p - 1)}>
          <PaginationLink previous />
        </PaginationItem>
      )}
      {getPaginationItem().map((paginationItem) => (
        <PaginationItem
          key={paginationItem}
          active={page === paginationItem}
          onClick={() => onPageChange(paginationItem)}
        >
          <PaginationLink>
            {paginationItem}
          </PaginationLink>
        </PaginationItem>
      ))}
      {hasNextPage && (
        <PaginationItem onClick={() => onPageChange((p) => p + 1)}>
          <PaginationLink next />
        </PaginationItem>
      )}
      {page < totalPages && (
        <PaginationItem onClick={() => onPageChange(totalPages)}>
          <PaginationLink last />
        </PaginationItem>
      )}
    </Pagination>
  );
}

export default PaginationComponent;
