import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Row } from 'reactstrap';

import api from '../../services/api';

import './styles.css';

function Category() {
  const [categorias, setCategorias] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/categorias');
        setCategorias(data);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  return (
    <Row className="px-2 py-5 px-sm-4 align-items-center justify-content-center category-container">
      {categorias.map(
        (categoria) => !categoria.categoria && categoria.image_url && (
          <Link to={`/${categoria._id}`} key={categoria._id}>
            <img src={categoria.image_url} alt={categoria.name} />
          </Link>
        ),
      )}
    </Row>
  );
}

export default Category;
