import React, { useEffect, useState, useCallback, useContext } from 'react';
import { useParams } from 'react-router-dom';
import Select from 'react-select';
import { Card, CardBody, CardHeader, Col, Container, CustomInput, FormGroup, Row } from 'reactstrap';

import { AppContext } from '../../contexts/app';
import api from '../../services/api';
import apiLocalidade from '../../services/api_localidade';
import AnuncioCard from '../AnuncioCard';
import Pagination from '../Pagination';

function AnuncioList() {
  const params = useParams();
  const { setLoading } = useContext(AppContext);
  const [animals, setAnimals] = useState([]);
  const [page, setPage] = useState(1);
  const [hasNextPage, setHasNextPage] = useState(false);
  const [hasPrevPage, setHasPrevPage] = useState(false);
  const [totalPages, setTotalPages] = useState(0);
  const [estados, setEstados] = useState([]);
  const [estado, setEstado] = useState('');
  const [loadingEstados, setLoadingEstados] = useState(false);
  const [cidades, setCidades] = useState([]);
  const [cidade, setCidade] = useState('');
  const [loadingCidades, setLoadingCidades] = useState(false);
  const [categoria, setCategoria] = useState('');
  const [categorias, setCategorias] = useState([]);
  const [idade, setIdade] = useState('');
  const [idades, setIdades] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/idades');
        setIdades(data);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/categorias');
        setCategorias(data);
        if (params.categoria) {
          setCategoria(params.categoria);
          setIdade('');
          setCidade('');
          setEstado('');
        }
      } catch (error) {
        console.log(error);
      }
    })();
  }, [params.categoria]);

  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        let url = `/animals?page=${page}&limit=10`;

        if (estado) {
          url = `${url}&estado=${estado.sigla}`;
        }

        if (cidade) {
          url = `${url}&cidade=${cidade.value}`;
        }

        if (idade) {
          url = `${url}&idade=${idade}`;
        }

        if (categoria) {
          url = `${url}&categoria=${categoria}`;
        }

        const { data } = await api.get(url);
        setAnimals(data.animals);
        setHasPrevPage(data.hasPrevPage);
        setHasNextPage(data.hasNextPage);
        setTotalPages(data.totalPages);
        setLoading(false);
      } catch (error) {
        console.log(error);
      }
    })();
  }, [page, estado, cidade, idade, categoria]);

  const handleOnChangeState = useCallback((stateValue) => {
    setCidade(null);
    setEstado('');

    if (!stateValue?.value) {
      setCidades([]);
      return;
    }

    setEstado(stateValue);

    (async () => {
      try {
        setLoading(true);
        setLoadingCidades(true);
        const { data = [] } = await apiLocalidade.get(
          `estados/${stateValue.value}/municipios`,
        );
        setCidades(
          data.map((c) => ({
            value: c?.nome,
            label: c?.nome,
            region: c?.microrregiao?.mesorregiao?.nome,
          })),
        );
        setLoading(false);
        setLoadingCidades(false);
      } catch (error) {
        setLoading(false);
        setLoadingCidades(false);
        console.log(error);
      }
    })();
  }, []);

  const populateStates = useCallback(() => {
    if (!estados.length) {
      (async () => {
        try {
          setLoading(true);
          setLoadingEstados(true);
          const { data = [] } = await apiLocalidade.get('estados?orderBy=nome');
          setEstados(
            data.map((s) => ({
              value: s?.id,
              label: s?.nome,
              sigla: s?.sigla,
            })),
          );
          setLoading(false);
          setLoadingEstados(false);
        } catch (error) {
          setLoading(false);
          setLoadingEstados(false);
          console.log(error);
        }
      })();
    }
  }, []);

  const handleOnChangeCity = useCallback((cityValue) => {
    if (!cityValue) {
      setCidade(null);
      return;
    }

    setCidade(cityValue);
  }, []);

  const selectStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: '#fff',
      borderRadius: '0.75rem',
      padding: '0.3rem',
      fontSize: '1.05rem',
    }),
    option: (styles) => ({
      ...styles,
      cursor: 'default',

      ':active': {
        ...styles[':active'],
        color: '#fff',
        backgroundColor: '#846446',
      },

      ':hover': {
        ...styles[':hover'],
        color: '#fff',
        backgroundColor: '#846446',
      },
    }),
    menu: (styles) => ({
      ...styles,
    }),
  };

  return (
    <Container id="anuncios">
      <Row>
        <Col md={3}>
          <Card>
            <CardHeader>
              <h5>Categorias</h5>
            </CardHeader>
            <CardBody>
              <FormGroup>
                <CustomInput
                  type="radio"
                  label="Todas"
                  id="categoria"
                  value=""
                  checked={!categoria}
                  onChange={({ target: { value } }) => setCategoria(value)}
                />
              </FormGroup>
              {categorias.map((c) => !c.categoria && (
                <FormGroup key={c._id}>
                  <CustomInput
                    type="radio"
                    id={c._id}
                    label={c.name}
                    value={c._id}
                    checked={c._id === categoria}
                    onChange={({ target: { value } }) => setCategoria(value)}
                  />
                </FormGroup>
              ))}
            </CardBody>
          </Card>
          <Card>
            <CardHeader>
              <h5>Idades</h5>
            </CardHeader>
            <CardBody>
              <FormGroup>
                <CustomInput
                  type="radio"
                  label="Todas"
                  id="idade"
                  value=""
                  checked={!idade}
                  onChange={({ target: { value } }) => setIdade(value)}
                />
              </FormGroup>
              {idades.map((i) => (
                <FormGroup key={i._id}>
                  <CustomInput
                    type="radio"
                    id={i._id}
                    label={i.name}
                    value={i._id}
                    checked={i._id === idade}
                    onChange={({ target: { value } }) => setIdade(value)}
                  />
                </FormGroup>
              ))}
            </CardBody>
          </Card>
          <Card>
            <CardHeader>
              <h5>Localidade</h5>
            </CardHeader>
            <CardBody>
              <Select
                className="basic-single mb-3"
                classNamePrefix="select"
                placeholder="Estado"
                noOptionsMessage={() => 'Nenhuma estado encontrado'}
                loadingMessage={() => 'Carregando estados'}
                isLoading={loadingEstados}
                isClearable
                isSearchable
                value={estado}
                options={estados}
                styles={selectStyles}
                onFocus={populateStates}
                onChange={handleOnChangeState}
              />
              <Select
                className="basic-single mb-3"
                classNamePrefix="select"
                placeholder="Cidade"
                noOptionsMessage={() => (!estado ? 'Selecione um estado' : 'Nenhuma cidade encontrada')}
                loadingMessage={() => 'Carregando cidades'}
                isLoading={loadingCidades}
                isClearable
                isSearchable
                value={cidade}
                styles={selectStyles}
                options={cidades}
                onChange={handleOnChangeCity}
              />
            </CardBody>
          </Card>
        </Col>
        <Col>
          {animals.map(({ anuncio, ...animal }) => (
            <AnuncioCard key={animal._id} anuncio={anuncio} animal={animal} />
          ))}

          <Row className="justify-content-center">
            <Pagination
              page={page}
              hasNextPage={hasNextPage}
              hasPrevPage={hasPrevPage}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </Row>
        </Col>
      </Row>
    </Container>
  );
}

export default AnuncioList;
