import React, { useState, useCallback, useRef } from 'react';
import { Button, Col, Container, FormFeedback, FormGroup, Input, Row } from 'reactstrap';

import * as Yup from 'yup';

import api from '../../services/api';
import Notification from '../Notification';

function NewEmail() {
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [validationErrors, setValidationErrors] = useState({});
  const notificationRef = useRef();

  const handleSubmit = useCallback(() => {
    (async () => {
      try {
        const data = {
          email,
          name,
        };

        const schema = Yup.object().shape({
          email: Yup.string()
            .email('E-mail inválido')
            .required('Informe um email'),
          name: Yup.string().required('Informe um nome'),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        // validation passed
        await api.post('/emails', data);

        notificationRef.current.notify({
          message: 'Cadastrado com sucesso',
        });

        setEmail('');
        setName('');
        setValidationErrors({});
      } catch (err) {
        console.log(err);
        setValidationErrors({});
        // Validation failed
        if (err instanceof Yup.ValidationError) {
          notificationRef.current.notify({
            message: 'Precisa informar email e nome para receber notícias',
            color: 'warning',
          });
          const ve = {};
          err.inner.forEach((e) => {
            ve[e.path] = e.message;
          });
          setValidationErrors(ve);
          return;
        }

        if (err && err.response && err.response.data) {
          const { message } = err.response.data;

          if (message) {
            notificationRef.current.notify({
              message,
              color: 'danger',
            });

            return;
          }
        }

        notificationRef.current.notify({
          message: 'Falha ao cadastrar nome e email',
          color: 'danger',
        });
      }
    })();
  }, [email, name]);

  return (
    <Container className="p-2 p-sm-5" fluid>
      <Notification ref={notificationRef} />
      <Row className="justify-content-center">
        <h1>Receba novidades por e-mail.</h1>
      </Row>
      <Row className="justify-content-center">
        <h5>Deixe o seu e-mail aqui e receba nossas novidades do mercado</h5>
      </Row>
      <Row className="p-3">
        <Col sm={5}>
          <FormGroup>
            <Input
              type="name"
              placeholder="Nome"
              value={name}
              invalid={!!validationErrors.name}
              onChange={({ target: { value } }) => setName(value)}
            />
            <FormFeedback>{validationErrors.name}</FormFeedback>
          </FormGroup>
        </Col>
        <Col sm={5}>
          <FormGroup>
            <Input
              type="email"
              placeholder="Email"
              value={email}
              invalid={!!validationErrors.email}
              onChange={({ target: { value } }) => setEmail(value)}
            />
            <FormFeedback>{validationErrors.email}</FormFeedback>
          </FormGroup>
        </Col>
        <Col sm={2}>
          <Button color="secondary" onClick={handleSubmit} block>
            Cadastrar
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

export default NewEmail;
