import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, CardBody, Col, Row, Button, Badge } from 'reactstrap';

import semimagem from '../../assets/img/semimage.gif';
import vendidoimg from '../../assets/img/vendido.png';
import MoneyFormat from '../Formatters/MoneyFormatter';

function AnuncioCard({
  anuncio: {
    _id: anuncioId,
    titulo,
    descricao,
    createAt,
    compartilhado,
    favoritado,
    reportado,
    visualizado,
    vendido,
  },
  animal: {
    _id,
    images,
    categorias,
    estado,
    idade,
    preco,
    quantidade,
    unidade,
  },
}) {
  const navigation = useNavigate();

  if (!anuncioId) {
    return null;
  }

  return (
    <Row>
      <Col>
        <Card>
          <CardBody>
            <Row>
              <Col xs={12} md={3}>
                <div className="position-relative">
                  {vendido && (
                    <img
                      src={vendidoimg}
                      alt="vendido"
                      className="position-absolute top-4"
                      style={{
                        width: '100%',
                        maxWidth: '300px',
                      }}
                    />
                  )}
                  {images?.[0]?.image_url ? (
                    <img
                      src={images?.[0]?.image_url}
                      className={vendido ? 'opacity-4' : ''}
                      alt={descricao}
                      style={{
                        width: '100%',
                        maxWidth: '300px',
                      }}
                    />
                  ) : (
                    <img
                      src={semimagem}
                      className={vendido ? 'opacity-4' : ''}
                      alt="sem imagem"
                      style={{
                        width: '100%',
                        maxWidth: '300px',
                      }}
                    />
                  )}
                </div>
              </Col>
              <Col xs={12} md={6}>
                <div className="p-2">
                  <Row className="mb-2">
                    {categorias.map((categoria) => (
                      <span key={categoria._id}>
                        <Badge color="secondary" className="mr-1">
                          {categoria.name}
                        </Badge>
                      </span>
                    ))}
                  </Row>
                  <Row className="justify-content-between mb-2">
                    <h4>{titulo}</h4>
                    <i className="far fa-heart" />
                  </Row>
                  <Row className="align-items-center mb-2">
                    <i className="fas fa-map-marker-alt mr-2" />
                    {estado}
                    <i className="fas fa-clock ml-3 mr-2" />
                    {new Intl.DateTimeFormat('pt-BR', {
                      weekday: 'long',
                      year: 'numeric',
                      month: 'long',
                      day: 'numeric',
                    }).format(new Date(createAt))}
                  </Row>
                  <Row>
                    <h6>{descricao}</h6>
                  </Row>
                </div>
              </Col>
              <Col xs={12} md={3}>
                <div className="p-2">
                  {compartilhado ? (
                    <Row>
                      <strong>Compartilhado:</strong>
                      {` ${compartilhado}`}
                    </Row>
                  ) : null}
                  {favoritado ? (
                    <Row>
                      <strong>favoritado:</strong>
                      {` ${favoritado}`}
                    </Row>
                  ) : null}
                  {reportado ? (
                    <Row>
                      <strong>Reportado:</strong>
                      {` ${reportado}`}
                    </Row>
                  ) : null}
                  {visualizado ? (
                    <Row>
                      <strong>Visualizado:</strong>
                      {` ${visualizado}`}
                    </Row>
                  ) : null}
                  {idade && (
                    <Row>
                      <strong>Tipo:</strong>
                      {` ${idade.name}`}
                    </Row>
                  )}
                  {quantidade ? (
                    <Row>
                      <strong>Disponível:</strong>
                      {` ${quantidade}`}
                    </Row>
                  ) : null}
                  {preco ? (
                    <Row>
                      <h5 className="text-secondary">
                        <MoneyFormat>{preco}</MoneyFormat>
                      </h5>
                      {`(${unidade.name})`}
                    </Row>
                  ) : null}
                  {!vendido ? (
                    <Row>
                      <Button
                        color="secondary"
                        onClick={() => navigation(`anuncio/${_id}`)}
                      >
                        <i className="fas fa-eye mr-1" />
                        Ver anúncio
                      </Button>
                    </Row>
                  ) : null}
                </div>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
}

export default AnuncioCard;
